<template>
  <div class="main">
    <head-top :title="deviceList.length==0?'Add Devices':'Choose Devices'" goBack="true"></head-top>
    <div class="content" v-show="deviceList.length!==0">
      <div class="describe">Select the device you want to add</div>
      <ul class="list">
        <li v-show="!item.isBinding" v-for="(item, index) in deviceList" :key = "item.id" @click.stop="chooseItem(item, index)">
          <div class="text">
            <span :class="{selected: activeIndex === index}">{{item.name}}</span>
            <strong>{{item.mac}}</strong>
          </div>
          <div class="icon">
            <img v-show="activeIndex !== index" src="../../../assets/addevice/defunct@3x.png">
            <img v-show="activeIndex === index" src="../../../assets/addevice/click@3x.png">
          </div>
        </li>
      </ul>
      <div class="nextbtn" @click="next" :class="{disableBtn: validate.itemActive}">
         <span v-show="!adding">Next</span>
         <span v-show="adding">
           <img src="../../../assets/addevice/adding@3x.png">
           Adding...
         </span>
       </div>
    </div>

    <div class="scanning_content" v-show="deviceList.length===0">
      <span>Scanning for plug</span>
      <div class="warpper">
        <div class="pulse"></div>
        <div class="pulse2"></div>
        <div class="pulse3"></div>
        <img src="../../../assets/addevice/wifi_icon@3x.png">
      </div>
    </div>
    <!-- 弹窗 -->
    <messageBox setHeight="220" setType="confirm" v-show="messageBoxType==='inputPasswordOpreation'">
    <div slot="confirmColumn" class="common_alert">
      <div class="alert_icon">
        <span class="password"></span>
      </div>
      <div class="alert_describe lang-fix-top input_warpper ">
        {{$t('homePage.encryptedTips')}}
        <ul class="lang-fix-top inputList">
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <input type="text" v-model="password" maxlength="4" ref="inputValue">
        </ul>
      </div>
      <div class="alert_operation">
        <div class="cancle">
          <span @click="alertChoose(0)">{{$t('messageBox.cancel')}}</span>
        </div>
        <div class="determine">
          <span @click="alertChoose(1)">{{$t('messageBox.confirm')}}</span>
        </div>
      </div>
    </div>
  </messageBox>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
// import { openDeviceScanningRequest, closeDeviceScanningRequest, addDeviceConnectedByRouterRequest } from '../../service/method.js'
// import { deviceConnectedByRouterResponse, addDeviceConnectedByRouterResponse } from '../../service/getData.js'
import headTop from '../../../components/common/head'
import messageBox from '../../../components/common/messageBox'

export default {
  data () {
    return {
      timer: null, // 计时器
      deviceList: [],
      activeIndex: -1, // 选择序号
      deviceItem: null, // 选择项
      password: '', // 密码
      adding: false // 正在添加
    }
  },

  mounted () {
    // 开启路由器扫描设备
    // openDeviceScanningRequest(this.NAVIGATOR, this)

    // // 获取路由器连接的设备列表返回
    // deviceConnectedByRouterResponse(this.NAVIGATOR, this)

    // // 添加路由器连接的设备返回
    // addDeviceConnectedByRouterResponse(this.NAVIGATOR, this)
    setTimeout(() => {
      this.deviceList = [
        {
          name: 'Smart Plug',
          mac: 'ACCF235FA21A'
        },
        {
          name: 'Smart Plug2',
          mac: 'ACCF235FA21A'
        },
        {
          name: 'Smart Plug3',
          mac: 'ACCF235FA21A'
        }
      ]
    }, 3000)
    // 超时处理
    // this.timer = setTimeout(() => {
    //   // 关闭路由器扫描设备
    //   // closeDeviceScanningRequest(this.NAVIGATOR, this)
    //   this.$store.state.transitiondre = false
    //   this.$router.push({path: '/Me/adfailed'})
    //   clearTimeout(this.timer)
    // }, 15000)

    if (this.$root.ISDEBUG) {
      // window.deviceConnectedByRouterResponse(true, '[{"name":"卧室空调的插座","state":1,"mac":"00:00:00:00:01","encrypted":false,"isBinding":false,"wifiInfomation":{"ssid":"PHICOMM_startai","strength":4}}]')

      setTimeout(() => {
        // window.deviceConnectedByRouterResponse(true, '[{"name":"卧室空调的插座01","state":1,"mac":"00:00:00:00:01","encrypted":false,"isBinding":false,"wifiInfomation":{"ssid":"PHICOMM_startai","strength":4}},{"name":"卧室空调的插座","state":1,"mac":"00:00:00:00:00","encrypted":false,"isBinding":false,"wifiInfomation":{"ssid":"PHICOMM_startai","strength":4}},{"name":"卧室空调的插座","state":1,"mac":"00:00:00:00:02","encrypted":false,"isBinding":false,"wifiInfomation":{"ssid":"PHICOMM_startai","strength":4}}]')
      }, 3000)
    }
  },

  components: {
    headTop,
    messageBox
  },

  computed: {
    ...mapState([
      'messageBoxType'
    ]),

    validate () {
      return {
        itemActive: this.deviceItem === null
      }
    }
  },

  methods: {
    ...mapMutations([
      'MESSAGEBOX_SWITCH'
    ]),

    navjump () {
      if (!this.adding) {
        // 关闭路由器扫描设备
        // closeDeviceScanningRequest(this.NAVIGATOR, this)
        this.$store.state.transitiondre = false

        if (this.$route.params.findPage) {
          this.$router.go(-1)
        } else {
          this.$router.go(-2)
        }
      }
    },

    chooseItem (item, index) {
      if (index === this.activeIndex) {
        this.activeIndex = -1
        this.deviceItem = null
      } else {
        this.activeIndex = index
        this.deviceItem = item
      }
    },

    alertChoose (num) {
      switch (num) {
        case 0:
          this.MESSAGEBOX_SWITCH({type: this.messageBoxType})
          break
        case 1:
          this.MESSAGEBOX_SWITCH({type: this.messageBoxType})
          this.adding = true
          // 关闭路由器扫描设备
          // closeDeviceScanningRequest(this.NAVIGATOR, this)
          // 添加路由器连接的设备
          // addDeviceConnectedByRouterRequest(this.NAVIGATOR, this, this.deviceItem.mac, this.password)
          break
      }
    },

    next () {
      this.$router.push({path: '/Me/devicename'})
      // 是否有选择项
      if (!this.validate.itemActive) {
        // 界面处理
        this.$store.state.transitiondre = false

        // 是否加密
        if (this.deviceItem.encrypted) {
          this.MESSAGEBOX_SWITCH({type: 'inputPasswordOpreation'})
        } else {
          // console.log(this.$route.params.findPage)
          // if (this.$route.params.findPage) {
          //   console.log(555)
          // }
          this.adding = true
          // 关闭路由器扫描设备
          // closeDeviceScanningRequest(this.NAVIGATOR, this)
          // 添加路由器连接的设备
          // addDeviceConnectedByRouterRequest(this.NAVIGATOR, this, this.deviceItem.mac, '')
          if (this.$root.ISDEBUG) {
            setTimeout(() => {
              // this.$router.push({name: 'rename', params: {item: this.deviceItem}})
              // window.addDeviceConnectedByRouterResponse(true)
            }, 2000)
          }
        }
      }
    },

    inputHandle () {
      if (this.password.length >= 4) {
        this.$refs.inputValue.blur()
        console.log(123)
      }
    }
  },

  destroyed () {
    clearTimeout(this.timer)
  },

  watch: {
    password: 'inputHandle'
  }
}
</script>

<!-- Add  "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@import '../../../styles/mixin.less';
@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes pulse{
  from {
    transform: scale(1);
    opacity: 1.0;
  } to {
    transform: scale(2.5);
    opacity: 0.0;
  }
}
.main{
  .fs;
  z-index: 11;
  background-color: #FFF;
  .bluetooth{
    .wh(1.066667rem, 100%);
    margin-left: .2rem;
    img{
      height: .533333rem;
      vertical-align: middle;
    }
  }
  .lang-fix-top{
    position: relative;
    top: -10px;
  }
  .input_warpper{
    height: 40%;
    font-size: 30px;
    .inputList{
      .wh(100%, 80px);
      position: relative;
      li{
        .wh(19%, 100%);
        margin: 0 3%;
        // float: left;
        border-bottom: 2px solid #A5CFFF;
      }
      input{
        .wh(130%, 90%);
        .fs;
        background: transparent;
        padding: 28px 28px 0 48px;
        color: @themeColor;
        letter-spacing: 80px;
        font-size: 32px;
      }
    }
  }
  .content{
    .fs-noTop;
    .describe{
      .wh-l(100%, 90px);
      font-size: 24px;
      text-align: left;
      color: #585858;
      padding: 0 30px;
      border-bottom: 1px solid #D4E4E5;
    }
    .list{
      .wh(100%, auto);
      li{
        padding: 0 30px;
        .wh(100%, 140px);
        border-bottom: 1px solid #D4E4E5;
        overflow: hidden;
        text-align: left;
        font-size: 30px;
        padding-left: 20px;
        display: flex;
        justify-content: space-between;
        .selected{
          color: @themeColor;
        }
        .text{
          display: flex;
          flex-direction: column;
        }
        span{
          .wh(100%, 50%);
          line-height: 76px;
          display: block;
        }
        strong{
          .wh(70%, 50%);
          line-height: 60px;
          display: block;
          color: #cccccc;
        }
        .icon{
          align-self: center;
        }
        img{
          height: 35px;
        }
      }
    }
    .disableBtn{
      background: #7f7f7f !important;
    }
    .nextbtn{
      .wh(520px,100px);
      line-height: 100px;
      border-radius: 10px;
      background:  #13af68;
      text-align: center;
      margin: 30% auto 0 auto;
      span{
        font-size: 47px;
        color: #fff;
        vertical-align: middle;
        img{
          height: 34px;
          position: relative;
          top: 2px;
          left: -5px;
          transform-origin: center;
          animation: rotate 1s infinite;
        }
      }
    }
  }
  .scanning_content{
    .fs-noTop;
    span{
      .wh-l(100%, 60px);
      color: #595959;
      display: block;
      font-size: 36px;
      margin: 70px 0;
    }
    .warpper{
      .wh(285px, 285px);
      margin: 70% auto 0 auto;
      position: relative;
      img{
        .wh(285px, 210px);
      }
      .pulse, .pulse2, .pulse3{
        position: absolute;
        left: 0;
        top: -50px;
        z-index: -1;
        .wh(100%, 100%);
        border: 2px solid #C1E0FF;
        border-radius: 50%;
        animation: pulse 2s infinite;
      }
      .pulse2{
        animation: pulse 2s .35s infinite;
      }
      .pulse3{
        animation: pulse 2s 0.7s infinite;
      }
    }
  }
}

</style>



